<template>
  <div class="main">
    <div class="content-wrap">
      <div class="title-wrap">
        <h2>班级管理</h2>
      </div>
      <el-card>
        <div class="button-wrapper">
          <el-button @click="handleCreate()" style="float:right">
            <i class="el-icon-plus"></i>添加
          </el-button>
        </div>
        <div class="table-wrap">
          <el-table :data="tableData">
            <el-table-column type="index"></el-table-column>
            <el-table-column prop="name" label="班级" width="180"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" @click="getStudent(scope.row)">详情</el-button>
                <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="block">
          <el-pagination
            layout="prev,pager,next"
            @current-change="currentChange"
            :current-page="currentPage"
            :total="total"
            background
            :page-size="pageSize"
          ></el-pagination>
        </div>
      </el-card>
    </div>
    <el-dialog :visible.sync="dialogFormShow" :title="dialogStatus[flag]" width="500px" center>
      <el-form ref="dialog" :model="dialog" size="mini" :rules="checkRule()">
        <el-form-item label="名称" prop="name">
          <el-input v-model="dialog.name"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="flag==='edit'?editGrade():CreateGrade()">提交</el-button>
        <el-button type="primary" @click="dialogFormShow=false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  createGrade,
  updateGrade,
  deleteGrade,
  getGradeByPage
} from "@/api/grade";
import store from "@/store/index";

export default {
  name: "GradeManage",
  data() {
    return {
      tableData: [],
      dialogFormShow: false,
      dialog: {
        id: undefined,
        name: ""
      },
      dialogStatus: {
        create: "添加班级",
        edit: "修改班级"
      },
      flag: "",
    

      total: 0,
      pageSize: 5,
      currentPage: 1,

      rule_create: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }]
      },
      rule_edit: {
        name: [{ required: true, message: "请输入名称", trigger: "change" }]
      }
    };
  },
  created() {
    this.getClassByPage();
  },
  methods: {
    getClassByPage() {
      getGradeByPage(this.currentPage, this.pageSize)
        .then(resp => {
          if (resp) {
            this.tableData = resp.data.data;
            this.total = resp.data.total;
          }
        })
        .catch(error => {
          this.$message(error);
        });
    },

    currentChange(page) {
      this.currentPage = page;
      this.getClassByPage();
    },

    resetDialog(){
      this.dialog={
        id:undefined,
        name:''
      }
    },

    handleCreate() {
      this.flag = "create";
      this.dialogFormShow = true;
      this.resetDialog()
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
    },

    checkRule() {
      if (this.flag === "create") {
        return this.rule_create;
      } else {
        return this.rule_edit;
      }
    },

    CreateGrade() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          createGrade(this.dialog)
            .then(response => {
              if (response.status === 200) {
                this.dialogFormShow = false;
                this.$message({
                  type: "success",
                  message: "添加成功"
                });
                this.dialog.id = response.data.obj
                if(this.total%this.pageSize==0){
                  this.total+=1
                }
                this.tableData.unshift(this.dialog);
                store.dispatch("grade/getClasses");
              }
            })
            .catch(() => {
              this.$message.error("添加失败");
            });
        } else {
          return false;
        }
      });
    },

    getStudent(row) {
      this.$router.push("/manage/class/" + row.id);
    },

    handleEdit(row) {
      this.dialogFormShow = true;
      this.flag = "edit";
      this.dialog = Object.assign({},row)
      this.$nextTick(() => {
        this.$refs["dialog"].clearValidate();
      });
      
    },

    editGrade() {
      this.$refs["dialog"].validate(valid => {
        if (valid) {
          const dialogData = Object.assign({},this.dialog)
          updateGrade(dialogData)
            .then(response => {
              if (response.status === 200) {
                for(const v of this.tableData){
                  if(v.id === this.dialog.id){
                    const index = this.tableData.indexOf(v)
                    this.tableData.splice(index,1,this.dialog)
                    break
                  }
                }
                this.dialogFormShow = false; 
                this.$message({
                  message: "修改成功",
                  type: "success"
                });
                store.dispatch("grade/editClasses",this.dialog);
              }
            })
            .catch(() => {
              this.$message.error("添加失败");
            });
        } else {
          return false;
        }
      });
    },

    handleDelete(row) {
      this.$confirm("是否删除", "提示", {
        comfirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        deleteGrade(row.id)
          .then(response => {
            if (response.status === 200) {
              const index = this.tableData.indexOf(row)
              this.tableData.splice(index,1)
              this.$message({
                message: "删除成功",
                type: "success"
              });
              if (this.tableData.length == 0) {
                this.currentPage = this.currentPage - 1;
                this.getClassByPage()
              }
              store.dispatch("grade/deleteClasses",row);
            }
          })
          .catch(() => {
            this.$message("删除失败");
          });
      }).catch(()=>{
        return
      });
    }
  }
};
</script>

